@media only screen and (max-width : 768px){
    .timeline{
        flex-direction: column;
        &__experience, &__education{
            width: 100%;
            gap: 2rem;
        }
    }
}
  
  .vertical-timeline {
    display: flex;
    flex-direction: column;
    position: relative;
  }
  
  .vertical-timeline::before {
    content: "";
    position: absolute;
    // left: 25px;
    top: -14px;
    width: 3px;
    background: var(--yellow-theme-main-color);
    height: 100%;
  }
  
  .vertical-timeline-element {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    position: relative;
  }
  
  .vertical-timeline-element-icon {
    width: 40px;
    height: 40px;
    background: #222;
    border: 2px solid var(--yellow-theme-sub-text-color);
    color: var(--yellow-theme-main-color);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: 0;
    transform: translateX(-50%);
  }
  
  .vertical-timeline-element-content {
    // background: #222;
    padding: 15px;
    margin-left: 60px;
    border: 1px solid var(--yellow-theme-main-color);
    border-radius: 5px;
    width: 80%;
  }
  
  .vertical-timeline-element-title {
    font-size: 18px;
    font-weight: bold;
    color: var(--yellow-theme-main-color);
  }
  
  .vertical-timeline-element-subtitle {
    font-size: 16px;
    font-style: italic;
    color: var(--yellow-theme-sub-text-color);
  }
  
  .vertical-timeline-element-description {
    font-size: 14px;
    margin-top: 10px;
    color: var(--yellow-theme-sub-text-color);
  }
  
  .vertical-timeline-element-date {
    font-size: 12px;
    color: #999;
    display: block;
    margin-top: 10px;
  }
  