@media only screen and (max-width: 768px) {
    .about_content {
        flex-direction: column;
        align-items: center;
        // text-align: center;

        &_personalWrapper,
        &_servicesWrapper {
            width: 100%;
        }

        &_servicesWrapper {
            margin: 10px 0px;

            &_innerContent {
                width: 200px;
                height: 200px;
                background: radial-gradient(120px, var(--yellow-theme-main-color), transparent 120%);

                div:nth-child(1) {
                    position: absolute;
                    top: -15%;
                    left: 38%;
                }

                div:nth-child(2) {
                    position: absolute;
                    right: -11%;
                    top: 40%;
                }

                div:nth-child(3) {
                    position: absolute;
                    bottom: -12%;
                    left: 40%;
                }

                div:nth-child(4) {
                    position: absolute;
                    left: -12%;
                    top: 40%;
                }
            }
        }
    }
}

@media only screen and (max-width: 480px) {
    .about_content {
        padding: 10px;
        
        h3 {
            font-size: 1.2rem;
        }

        p {
            font-size: 0.9rem;
            line-height: 1.5;
        }

        &_personalWrapper {
            ul {
                padding: 0;
                li {
                    display: flex;
                    flex-direction: column;
                    // align-items: center;
                    font-size: 0.9rem;

                    span.title,
                span.value {
                    padding: 10px;
                }
                }
            }
        }

        &_servicesWrapper {
            margin: 10px 0px;

            &_innerContent {
                width: 200px;
                height: 200px;
                background: radial-gradient(120px, var(--yellow-theme-main-color), transparent 120%);

                div:nth-child(1) {
                    position: absolute;
                    top: -15%;
                    left: 38%;
                }

                div:nth-child(2) {
                    position: absolute;
                    right: -15%;
                    top: 40%;
                }

                div:nth-child(3) {
                    position: absolute;
                    bottom: -16%;
                    left: 40%;
                }

                div:nth-child(4) {
                    position: absolute;
                    left: -12%;
                    top: 40%;
                }
            }
        }
    }
}
